/*Advertencia de las 2 ultimas sesiones*/
.advertencias {
  background-color: #ffe6b3; /* Fondo de advertencia */
  padding: 10px;
  border-radius: 5px;
}

.introduccion p {
  margin: 0;
  font-size: 1.5em;
