.pregunta {
  margin-bottom: 20px;
  border: 1px solid #999;
  padding: 10px;
  border-radius: 5px;
}

.pregunta-header {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  margin-bottom: 10px;
}

.pregunta-texto {
  margin-bottom: 10px;
}

.opciones label {
  display: block;
  margin-bottom: 5px;
}

.resultado.correcto {
  color: green;
}

.resultado.incorrecto {
  color: red;
}

.finalizar-cuestionario {
  text-align: center;
  margin-top: 20px;
}

.finalizar-cuestionario button {
  padding: 12px 24px;
  background: linear-gradient(145deg, #4DB6AC, #00796B); /* Verde menta a verde pasto */
  color: #ffffff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: background 0.3s, box-shadow 0.3s, transform 0.2s;
  min-width: 120px;
  line-height: 1.5;
}

.finalizar-cuestionario button:hover {
background: linear-gradient(145deg, #4DB6AC, #004D40); /* Verde menta a verde bosque */

  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  transform: translateY(-2px);
}

.finalizar-cuestionario button:active {
  background: linear-gradient(145deg, #4DB6AC, #004D40); /* Verde menta a verde bosque */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(0);
}






.puntaje-total {
  text-align: center;
  margin-top: 20px;
  font-size: 1.2em;
  font-weight: bold;
}

.acciones {
  text-align: center;
  margin-top: 20px;
}

/* Estilos generales para los botones y enlaces */
.acciones a.btn-atras,
.acciones a.btn-repetir,
.acciones .btn-repetir,
.acciones a.btn-nextSesion {
  display: inline-block; /* Permite aplicar padding y margin */
  padding: 12px 24px; /* Espacio alrededor del texto */
  margin: 8px; /* Espacio entre los botones */
  border: none;
  border-radius: 8px; /* Bordes más redondeados */
  cursor: pointer;
  font-weight: 500;
  text-decoration: none !important; /* Elimina el subrayado del enlace */
  font-size: 16px !important; /* Tamaño de fuente ajustado para ser visible pero no tan grande */
  color: white !important; /* Color del texto */
  transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease; /* Transiciones suaves */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Sombra sutil para dar profundidad */
  text-align: center; /* Centra el texto dentro del enlace o botón */
  min-width: 120px; /* Ancho mínimo para igualar los botones */
  font-family: Arial, sans-serif; /* Fuente consistente para todos los botones */
  line-height: 1.5; /* Altura de línea para mejorar la legibilidad */
}

/* Estilos para el enlace btn-atras */
.acciones a.btn-atras {
  background: linear-gradient(145deg, #F5F5F5, #4B4B4B); /* Gris perla a gris antracita */
  color: #ffffff;
}

.acciones a.btn-atras:hover {
  background: linear-gradient(145deg, #E0E0E0, #333333); /* Degradado más oscuro al pasar el cursor */
  transform: scale(1.05); /* Efecto de aumento al pasar el cursor */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Sombra más pronunciada al pasar el cursor */
}

/* Estilos para el enlace btn-repetir y el botón btn-repetir */
.acciones a.btn-repetir,
.acciones .btn-repetir {
  background: linear-gradient(145deg, #00BFFF, #001F3F); /* Azul cielo a azul marino */
  color: #ffffff;
}

.acciones a.btn-repetir:hover,
.acciones .btn-repetir:hover {
  background: linear-gradient(145deg, #001F3F, #00008B); /* Azul marino a azul noche */

  transform: scale(1.05); /* Efecto de aumento al pasar el cursor */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Sombra más pronunciada al pasar el cursor */
}

/* Estilos para el enlace btn-nextSesion */
.acciones a.btn-nextSesion {
  background: linear-gradient(145deg, #76c7c0, #4CAF50); /* Degradado verde claro */
  color: #ffffff;
}

.acciones a.btn-nextSesion:hover {
  background: linear-gradient(145deg, #66b2a7, #3e8e41); /* Degradado más oscuro al pasar el cursor */
  transform: scale(1.05); /* Efecto de aumento al pasar el cursor */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Sombra más pronunciada al pasar el cursor */
}

/* Media query para pantallas pequeñas */
@media (max-width: 600px) {
  .acciones a.btn-atras,
  .acciones a.btn-repetir,
  .acciones .btn-repetir,
  .acciones a.btn-nextSesion {
    padding: 10px 20px; /* Ajuste del espacio alrededor del texto */
    font-size: 12px; /* Tamaño de fuente más pequeño para pantallas pequeñas */
    min-width: 100px; /* Ancho mínimo más pequeño para pantallas pequeñas */
  }
}








@media (max-width: 600px) {
  .introduccion p {
    font-size: 1.2em;
  }

  .finalizar-cuestionario button,
  .acciones .btn-repetir,
  .acciones .btn-atras {
    padding: 8px 16px;
    font-size: 0.9em;
  }
}

.mensaje-exito {
  background-color: #f2f5f8; /* Color de fondo suave y pastel */
  color: black; /* Color del texto en negro */
  padding: 20px; /* Espacio alrededor del texto */
  text-align: center;
  margin-top: 20px;
  border-radius: 8px; /* Bordes redondeados */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra sutil para dar profundidad */
}

.mensaje_aprobado{
  color: blue;
}

.mensaje_reprobado{
  color: red;
}



/* Estilos para el botón con degradado y efecto hover */
.btn-next {
  display: inline-block; /* Asegura que el botón esté en línea con el mensaje */
  padding: 12px 24px; /* Espacio alrededor del texto del botón */
  margin-top: 15px; /* Margen superior para separar el botón del texto */
  background: linear-gradient(145deg, #ff5722, #c62828); /* Degradado rojo cálido a rojo oscuro */
  color: white;
  border: none;
  border-radius: 5px; /* Bordes redondeados del botón */
  cursor: pointer;
  text-decoration: none;
  font-size: 16px; /* Tamaño de fuente más grande para mejor legibilidad */
  transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease; /* Transición suave para efectos */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Sombra sutil para dar profundidad */
}

.btn-next:hover {
  background: linear-gradient(145deg, #c62828, #a1281c); /* Degradado más oscuro al pasar el cursor */
  transform: scale(1.05); /* Efecto de aumento al pasar el cursor */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Sombra más pronunciada al pasar el cursor */
}



@keyframes mensajeExito {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
