/* Estilos del formulario */
.login-form {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semitransparente */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* Asegurarse de que esté por encima de otros elementos */
}

/* Estilos del formulario interno */
.login-form form {
  background-color: #ecf0f1; /* Fondo claro para el formulario */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px; /* Ancho máximo del formulario */
  box-sizing: border-box;
  margin: 0 15px; /* Espacio de cada lado */
}

/* Estilos del título del formulario */
.login-form h2 {
  margin-top: 0;
  margin-bottom: 20px; /* Espacio debajo del título */
  color: #34495e;
  text-align: center; /* Centra el texto del título */
  font-size: 24px; /* Tamaño del texto */
}

/* Estilos de las etiquetas del formulario */
.login-form label {
  display: block;
  margin-bottom: 15px;
  color: #2c3e50;
}

/* Estilos de los campos de entrada */
.login-form input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  box-sizing: border-box;
}

/* Estilos del botón de envío */
.login-form button {
  background-color: #3498db; /* Color de fondo del botón */
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
  margin-top: 10px;
}

.login-form button:hover {
  background-color: #2980b9; /* Color de fondo al pasar el mouse */
}

/* Estilos del mensaje de error */
.login-form .error {
  color: #e74c3c; /* Color del mensaje de error */
  margin-top: 10px;
  text-align: center; /* Centra el mensaje de error */
}

/* Adaptación a pantallas pequeñas */
@media (max-width: 480px) {
  .login-form form {
    padding: 15px;
    margin: 0 10px; /* Espacio de cada lado en pantallas pequeñas */
  }

  .login-form h2 {
    font-size: 20px;
    margin-bottom: 15px;
  }

  .login-form label {
    margin-bottom: 10px;
  }

  .login-form input {
    padding: 8px;
  }

  .login-form button {
    padding: 8px;
    font-size: 14px;
  }

  .login-form .error {
    font-size: 14px;
  }
}
