/* Footer.css */
.footer {
  background: linear-gradient(to right, #2ecc71, #3498db); /* Degradado de verde a azul */
  color: white;
  text-align: center;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0; /* Asegura que esté pegado al borde izquierdo */
  z-index: 1000;
}

.footer-content {
  max-width: 100%; /* Cambiado a 100% para ajustarse al ancho completo */
  margin: 0 auto;
}

.footer-content p {
  margin: 5px 0;
}
