/* Estilo para el contenedor del logo */
.logo-container {
  display: flex;
  flex-direction: column; /* Alinea los elementos en una columna */
  justify-content: center; /* Centra horizontalmente */
  align-items: center;     /* Centra verticalmente */
  height: 100vh;           /* Ocupa el 100% de la altura de la ventana */
  background-color: #f8f9fa; /* Color de fondo opcional */
  text-align: center;      /* Centra el texto horizontalmente */
}

/* Estilo para el logo */
.logo-404 {
  max-width: 90%;   /* Ajusta el tamaño máximo del logo en porcentaje */
  max-height: 50%;  /* Ajusta la altura máxima del logo en porcentaje */
  height: auto;     /* Mantiene la proporción del logo */
  width: auto;      /* Mantiene la proporción del logo */
}

/* Media Query para pantallas pequeñas */
@media (max-width: 600px) {
  .logo-404 {
    max-width: 70%;   /* Ajusta el tamaño máximo del logo en pantallas pequeñas */
    max-height: 40%;  /* Ajusta la altura máxima del logo en pantallas pequeñas */
  }
}

@media (max-width: 400px) {
  .logo-404 {
    max-width: 50%;   /* Ajusta aún más el tamaño máximo del logo en pantallas muy pequeñas */
    max-height: 30%;  /* Ajusta aún más la altura máxima del logo en pantallas muy pequeñas */
  }
}

/* Estilo para el texto de la página 404 */
h1 {
  font-size: 2rem; /* Tamaño de fuente del encabezado */
  margin: 1rem 0;  /* Márgenes superior e inferior del encabezado */
}

p {
  font-size: 1rem; /* Tamaño de fuente del párrafo */
  margin: 0.5rem 0; /* Márgenes superior e inferior del párrafo */
}
