/* Contenedor centrado */
.contenedor-centrado {
  display: flex;
  flex-direction: column; /* Asegura que los elementos hijos se apilen verticalmente */
  align-items: center; /* Centra horizontalmente */
  padding: 10px;
  min-height: 100vh; /* Asegura que el contenedor ocupe toda la altura de la pantalla */
  background-color: #f0f0f5; /* Fondo suave para mejorar el contraste */
}

/* Estilos para los párrafos informativos */
.parrafos-informativos {
  width: 100%; /* Asegura que ocupe el 100% del ancho disponible */
  max-width: 600px; /* Limita el ancho máximo */
  text-align: center; /* Centra el texto */
  margin-bottom: 40px; /* Espacio debajo de los párrafos */
}

.parrafo-menuP-leccion{
  font-weight: bold;
  font-size: 2em;
}

/* Contenedor del menú de prácticas */
.contenedor-menuPracticas {
  width: 100%; /* Ajusta el ancho según lo que necesites */
  max-width: 600px; /* Limita el ancho máximo */
  box-sizing: border-box; /* Incluye el padding y el border en el ancho y alto total */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Sombra de borde */
  border: 1px solid #ccc; /* Borde */
  border-radius: 10px; /* Bordes redondeados */
  padding: 20px; /* Espacio interno */
  background-color: #fff; /* Fondo blanco para un mejor contraste */
}

/* Introducción */
.introduccion-practicas-primera,
.introduccion-practicas-segunda,
.introduccion-practicas-tercera,
.introduccion-practicas-final {
  border-radius: 8px;
  margin-bottom: 20px; /* Espacio debajo de la introducción */
  margin-top: 20px; /* Espacio arriba de la introducción */
  padding: 10px; /* Espacio interno */
}

.introduccion-practicas-primera{
  background-color: #fbebe2;
}
.introduccion-practicas-segunda{
  background-color: #e9f5e8;
}
.introduccion-practicas-tercera{
  background-color: #ffeedb;
}
.introduccion-practicas-final{
  background-color: #d11919;
}



.introduccion-practicas-primera h2,
.introduccion-practicas-segunda h2,
.introduccion-practicas-tercera h2,
.introduccion-practicas-final h2{
  margin: 0;
  font-size: 20px;
  text-align: center;
}

.introduccion-practicas-primera h2{
  color: #e74c3c;
}
.introduccion-practicas-segunda h2{
  color: #388e3c;
}
.introduccion-practicas-tercera h2{
  color: #f57c00;
}
.introduccion-practicas-final h2{
  color: #fff;
}


/* Enlaces a prácticas */
.contenedor-practica {
  display: block; /* Asegura que el enlace ocupe todo el espacio del contenedor */
  margin: 25px 0; /* Ajusta el margen entre los enlaces */
  padding: 25px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  transition: transform 0.2s, box-shadow 0.2s; /* Transiciones suaves */
  text-decoration: none; /* Sin subrayado en los enlaces */
  color: inherit; /* Hereda el color del texto */
  box-shadow: none; /* Elimina cualquier sombra extra */
  outline: none; /* Elimina el borde de enfoque */
}

.contenedor-practica:hover {
  transform: translateY(-5px); /* Efecto de elevación al pasar el mouse */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Sombra más pronunciada al pasar el mouse */
}

.contenedor-practica h1 {
  margin-top: 0;
  font-size: 24px;
  text-align: center;
}

.contenedor-practica p {
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  margin-bottom: 0; /* Elimina el margen inferior */
}
