header {
  background: linear-gradient(to right, #3498db, #2ecc71); /* Degradado de azul a verde */
  color: white;
  padding: 15px 20px;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: fixed;
  width: 100%; /* Ancho completo */
  top: 0;
  left: 0; /* Asegura que esté pegado al borde izquierdo */
  z-index: 1000;
}

header div {
  margin: 0 auto; /* Centrar el contenido dentro del header */
  max-width: 100%; /* Ajusta el ancho máximo para asegurar que no se desborde */
}
